.swiper-container {
	width: 100%;
	height: 300px;
	margin-left: auto;
	margin-right: auto;
}

.swiper-slide {
	background-size: cover;
	background-position: center;
	display: -webkit-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	-webkit-justify-content: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	-webkit-align-items: center;
	align-items: center;
	border-radius: 4px;
	overflow: hidden;
	position: relative;
	padding-top: 66.66%;
	height: 0 !important;
}

.swiper-slide img {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	width: 100%;
	height: 100%;
	-o-object-fit: cover;
	object-fit: cover;

	text-indent: -9999px;
}

.gallery-top {
	height: 100%;
	width: 100%;
	border-radius: 4px;
}

.gallery-thumbs {
	box-sizing: border-box;
	height: auto;
}

.gallery-thumbs .swiper-slide {
	height: 100%;
	opacity: 0.4;

	padding-top: calc(66.66% / 5) !important;
}

.gallery-thumbs .swiper-slide-thumb-active {
	opacity: 1;
}

.post-slider {
	max-width: 100%;
	@media screen and (min-width: 1024px) {
		background: white;
		box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.1);
		max-width: calc(100vw - 360px);
		padding: 32px 40px;
	}
	@media screen and (min-width: 1340px) {
		max-width: 985px;
		padding: 32px 40px;
	}
}
