.digest {
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 80px;
    padding: 0 30px;
    background: #5d86ac;
    @media screen and (max-width: 1023px) {
      flex-direction: column-reverse;
      text-align: center;
      padding: 20px;
    }
  }
  &__reviews {
    padding-top: 60px;
    background: #fafafa;
    .subscription__subtitle {
      margin-bottom: 40px;
      font-size: 24px !important;
      margin-top: 0;
      @media screen and (max-width: 1023px) {
        text-align: center;
      }
    }
  }

  &__years {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    margin: 0;
    padding: 0;
    color: white;
    @media screen and (max-width: 1023px) {
      margin-bottom: 10px;
    }
    li {
      margin-right: 20px;
      font-size: 14px;
      opacity: 0.6;
      cursor: pointer;
      transition: all 0.25s ease;
      font-weight: bold;
      @media screen and (max-width: 1023px) {
        margin: 0 10px;
      }
      padding: 6px 15px;
      background: #eaeaea;
      margin: 5px 10px !important;
      border-radius: 20px;
    }
    & li.active {
      color: white;
      background:var(--main-accent-color);
    }
    & li.active,
    & li:hover {
      opacity: 1;
    }
  }

  &__subscription {
    // padding: 15px 30px;
    border: none;
    border-radius: 3px;
    cursor: pointer;

    @media screen and (max-width: 480px) {
      width: 100%;
      padding: 0;
    }
  }

  &__wrapper {
    padding: 40px 30px 20px;
  }

  &__months {
  }

  &__month {
    margin: 0;
    padding: 0;
    flex-wrap: wrap;
    justify-content: space-between;
    list-style: none;
    display: none;
    &.active {
      display: grid;
      gap: 10px;
      grid-template-columns: repeat(4, 1fr);
      @media screen and (max-width: 640px) {
        grid-template-columns: repeat(3, 1fr);
      }
      @media screen and (max-width: 480px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @media screen and (max-width: 374px) {
        grid-template-columns: 1fr;
      }
    }
    a {
      text-decoration: none;
      color: black;
      display: flex;
      flex-direction: column;
    }
    img {
      max-width: 100%;
      box-shadow: none !important;
      margin-bottom: 10px;
      object-fit: contain;
    }
    li {
      // width: calc(100% / 6 - 15px);
      width: 100%;
      margin-bottom: 20px;
      text-align: center;
      font-size: 14px;


      // @media screen and (max-width: 1023px) {
      //   width: calc(100% / 4 - 15px);
      // }
      // @media screen and (max-width: 480px) {
      //   width: calc(100% / 2 - 15px);
      // }
      // @media screen and (max-width: 374px) {
      //   width: 100%;
      // }
    }
  }
}
