.header_search {
  background-color: #23262f;
  margin-bottom: 40px;
  display: none;
  min-height: 64.5px;
  top: 0;
  position: fixed;
  width: 100%;
  z-index: 100;
}

.header_search .small-3 {
  border-right: 2px solid #fff;
}

.header_search .logo {
  padding: 19px 0;
  position: static;
}

.header_search .search_input {
  padding-top: 14px;
}

.header_search .search_input button {
  position: static;
  width: auto;
  height: auto;
  padding: 0;
  background-color: #23262f;
  cursor: pointer;
}

.header_search .search_input input {
  display: inline-block;
  max-width: 700px;
  border: 0;
  background-color: #23262f;
  color: #fff;
  margin-bottom: 0;
  box-shadow: none;
}

.header_search .search_input input:focus {
  outline: 0;
  box-shadow: none;
}

.header_search .search_input input::placeholder {
  color: rgba(255, 255, 255, 0.42);
  font-weight: 500;
}

.header_search .search_input i {
  color: #fff;
  font-size: 18px;
}

.header_search .search_input #close {
  float: right;
  margin-top: 9px;
  cursor: pointer;
}
